<template>
    <section class="py-5">
        <div class="container">
            <div class="row">
                <div class="col-12 col-md-6 col-lg-3"
                     v-for="product in products"
                     :key="product.id"
                >
                    <figure class="card shadow-sm">
                        <div class="product-img" style="height: 200px;min-width: 291px">

                        </div>
                        <figcaption class="p-3 text-start border-top">
                            <div class="price">
                                <span class="" v-text="formatCurrency(product.price)"></span>
                            </div>
                            <router-link class="text-decoration-none" :to="{name: 'products.detail',params: {slug: product.slug}}">
                                <p class="title mb-0" v-text="product.name"></p>
                            </router-link>
                            <p class="text-black-50" v-text="product.category.name">
                            </p>
                            <div class="">
                                <a class="btn btn-secondary me-2" :class="product.inventory === 0 ? 'disabled' : ''"
                                   v-text="(product.inventory === 0? 'Out Of Stock' : 'Add To Cart')"
                                >
                                </a>
                                <a href="" class="btn btn-light">
                                    <i class="fa fa-heart"></i>
                                </a>
                            </div>
                        </figcaption>
                    </figure>
                </div>
            </div>
        </div>
    </section>
</template>
<script>
export default {
    computed: {
        products(){
            return this.$store.state.products.filter(product => product.category.slug === this.$route.params.category)
        }
    },
    methods: {
        formatCurrency(price){
            price = (price/100);
            return price.toLocaleString("en-US",{style: "currency",currency: "USD"})
        }
    }
}
</script>
